import React, { useEffect, useState } from "react";
import "./Dashboard.css";
import logoImage from "./Components/dd0739c0-6b9d-47ef-b400-6a6582dcf025.jpeg";
import axios from "axios";
import "./App.css";
import Table from "./Components/Table";
import { Collapse } from "bootstrap";
import "react-date-range/dist/styles.css";
import "react-date-range/dist/theme/default.css";
import "./Components/DateRange"
import { DateRangePicker } from 'react-date-range';
import { DateRange } from "@mui/icons-material";

function Dashboard() {
  const [API1Data, setAPI1Data] = useState([]);
  const [API2Data, setAPI2Data] = useState([]);

  const API = process.env.API1;
  const API2 = process.env.API2;

  const fetchAPI1Data = async () => {
    try {
      if (!API) {
        console.error("API1 URL is not defined");
        return;
      }
  
      const res = await axios.get(API);
      const data = res.data;
      if (data.length > 0) {
        setAPI1Data(data);
      }
    } catch (e) {
      console.error(e);
    }
  };
  
  const fetchAPI2Data = async () => {
    try {
      if (!API2) {
        console.error("API2 URL is not defined");
        return;
      }
  
      const res = await axios.get(API2);
      setAPI2Data(res.data);
    } catch (error) {
      console.error("Error fetching API2 data:", error);
    }
  };
  

  const autoRefresh = () => {
    // Refresh every 2 seconds
    const intervalId = setInterval(() => {
      fetchAPI2Data();
      fetchAPI1Data();
    }, 2000);

    // Clear the interval when the component unmounts
    return () => clearInterval(intervalId);
  };

  useEffect(() => {
    fetchAPI2Data();
    fetchAPI1Data();

    // Start auto-refresh on component mount
    const intervalId = autoRefresh();

    // Clean up interval on component unmount
    return () => clearInterval(intervalId);
  }, []);

  const [style, setStyle] = useState(
    "navbar-nav  sidebar sidebar-dark accordion"
  );

  const changeStyle = () => {
    if (
      style === "navbar-nav bg-gradient-primary sidebar sidebar-dark accordion"
    ) {
      setStyle(
        "navbar-nav bg-gradient-primary sidebar sidebar-dark accordion toggled"
      );
    } else {
      setStyle("navbar-nav bg-gradient-s sidebar sidebar-dark accordion");
    }
  };
  const changeStyle1 = () => {
    if (
      style === "navbar-nav bg-gradient-primary sidebar sidebar-dark accordion"
    ) {
      setStyle(
        "navbar-nav bg-gradient-primary sidebar sidebar-dark accordion toggled1"
      );
    } else {
      setStyle("navbar-nav bg-gradient-primary sidebar sidebar-dark accordion");
    }
  };

  return (
    <div>
      <body id="page-top">
        <div id="wrapper">
          <ul
            style={{ backgroundColor: "#222d2f" }}
            className={style}
            id="accordionSidebar "
          >
            <a
              className="sidebar-brand d-flex align-items-center justify-content-center"
              href="#"
            >
              <div
                style={{ backgroundColor: "#222d2f" }}
                className=" sidebar-brand-icon rotate-n-15"
              ></div>
              <div className="sidebar-brand-text bg-danger">
                <div>
                  <div>
                    <img
                      style={{ width: "224px", height: "70px" }}
                      src={logoImage}
                      alt="Logo"
                      className="logo-image-Dashboard"
                    />
                  </div>
                </div>
              </div>
              <div className="text-center  d-none d-md-inline "></div>
            </a>

            {/*   <!-- Divider --> */}
            <hr className=" sidebar-divider my-0" />

            {/*  <!-- Nav Item - Dashboard --> */}
            <li className="nav-item active ">
              <a className="nav-link" href="index.html">
                <i className="fas fa-fw fa-tachometer-alt"></i>
                <span>Dashboard</span>
              </a>
            </li>
          </ul>

          <div id="content-wrapper" className="d-flex flex-column">
            {/*  <!-- Main Content --> */}
            <div id="content ">
              <nav
                style={{ backgroundColor: "#222d2f" }}
                className="navbar text-center text-white navbar-expand navbar-light topbar mb-4 static-top shadow"
              >
                <div className="mx-auto">
                  <h3>
                    {" "}
                    <b>
                      Smart parking management with automated vehicle number
                      plate detection
                    </b>
                  </h3>
                </div>
              </nav>
            </div>

            <Table />

            <footer className="sticky-footer bg-white">
              <div className="container my-auto">
                <div className="copyright text-center my-auto">
                  <span>Copyright &copy; Shivainfotech 2024</span>
                </div>
              </div>
            </footer>
          </div>
        </div>
      </body>
    </div>
  );
}

export default Dashboard;
