import React, { useEffect, useState } from "react";
import axios from "axios";
import PropTypes from "prop-types";
import Box from "@mui/material/Box";
import Collapse from "@mui/material/Collapse";
import IconButton from "@mui/material/IconButton";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import QRCode from "qrcode.react";

import { DateRangePicker } from "react-date-range";

const createParkingData = (ParkingId, Total, Reserve, Available) => ({
  ParkingId,
  Total,
  Reserve,
  Available,
});

const createVehicleData = (
  RecId,
  ParkingId,
  TicketQR,
  VehicleNo,
  ParkedType,
  StartParkedTime,
  EndParkedTime,
  AccountType,
  ParkedAmount
) => ({
  RecId,
  ParkingId,
  TicketQR,
  VehicleNo,
  ParkedType,
  StartParkedTime,
  EndParkedTime,
  AccountType,
  ParkedAmount,
});

function Row(props) {
  const { row, row2 } = props;
  const [open, setOpen] = React.useState(false);

  return (
    <React.Fragment>
      <TableRow
        style={{ border: "1px solid #000000" }}
        sx={{ "& > *": { borderBottom: "unset" } }}
      >
        <TableCell style={{ border: "1px solid #000000", width: "100px" }}>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
          >
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell
          style={{ border: "1px solid #000000", width: "500px" }}
          component="th"
          scope="row"
        >
          {row.ParkingId}
        </TableCell>
        <TableCell
          style={{ border: "1px solid #000000", width: "500px" }}
          align="left"
        >
          {row.Total}
        </TableCell>
        <TableCell
          style={{ border: "1px solid #000000", width: "500px" }}
          align="left"
        >
          {row.Reserve}
        </TableCell>
        <TableCell
          style={{ border: "1px solid #000000", width: "500px" }}
          align="left"
        >
          {row.Available}
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
              <Typography
                align="left"
                variant="h6"
                gutterBottom
                component="div"
              >
                Vehicle Details
              </Typography>
              <Table size="small" aria-label="vehicle-details">
                <TableHead>
                  <TableRow align="left">
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      RecId
                    </TableCell>
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      ParkingId
                    </TableCell>
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      TicketQR
                    </TableCell>
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      VehicleNo
                    </TableCell>
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      ParkedType
                    </TableCell>
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      StartParkedTime
                    </TableCell>
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      EndParkedTime
                    </TableCell>
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      AccountType
                    </TableCell>
                    <TableCell
                      style={{
                        border: "1px solid #000000",
                        backgroundColor: "#4f4f4f",
                        color: "white",
                        width: "414px",
                      }}
                      align="left"
                    >
                      ParkedAmount
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody style={{ border: "1px solid #000000" }}>
                  {row2.map((vehicle) => (
                    <TableRow
                      style={{ border: "1px solid #000000" }}
                      key={vehicle.RecId}
                    >
                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        {vehicle.RecId}
                      </TableCell>
                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        {vehicle.ParkingId}
                      </TableCell>
                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        <QRCode value={vehicle.TicketQR} />
                      </TableCell>

                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        {vehicle.VehicleNo}
                      </TableCell>
                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        {vehicle.ParkedType}
                      </TableCell>
                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        {vehicle.StartParkedTime
                          ? vehicle.StartParkedTime.split("T") + "  "
                          : ""}
                      </TableCell>
                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        {vehicle.EndParkedTime
                          ? vehicle.EndParkedTime.split("T") + "  "
                          : ""}
                      </TableCell>
                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        {vehicle.AccountType === "A"
                          ? "A (Postpaid)"
                          : vehicle.AccountType === "B"
                          ? "B (Prepaid)"
                          : vehicle.AccountType}
                      </TableCell>
                      <TableCell
                        style={{ border: "1px solid #000000" }}
                        align="left"
                      >
                        {vehicle.ParkedAmount}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

Row.propTypes = {
  row: PropTypes.shape({
    ParkingId: PropTypes.string.isRequired,
    Total: PropTypes.number.isRequired,
    Reserve: PropTypes.number.isRequired,
    Available: PropTypes.number.isRequired,
  }).isRequired,
  row2: PropTypes.arrayOf(
    PropTypes.shape({
      RecId: PropTypes.string.isRequired,
      ParkingId: PropTypes.string.isRequired,
      TicketQR: PropTypes.string.isRequired,
      VehicleNo: PropTypes.string.isRequired,
      ParkedType: PropTypes.string.isRequired,
      StartParkedTime: PropTypes.string.isRequired,
      EndParkedTime: PropTypes.string.isRequired,
      AccountType: PropTypes.string.isRequired,
      ParkedAmount: PropTypes.string.isRequired,
    })
  ).isRequired,
};

export default function CollapsibleTable() {
  const [rows, setRows] = useState([]);
  const [rows2, setRows2] = useState([]);
  const [originalVehicleData, setOriginalVehicleData] = useState([]);
  const [filteredVehicleData, setFilteredVehicleData] = useState([]);
  const [dateRange, setDateRange] = useState([
    {
      startDate: new Date(),
      endDate: new Date(),
      key: "selection",
    },
  ]);

  // useEffect(() => {
  //   const fetchData = async () => {
  //     try {
  //       const response = await axios.get(
  //         "http://52.66.50.76:8080/api/CKTCTR/GetParkingData?ParkingId="
  //       );

  //       const apiData = response.data;

  //       const updatedRows = apiData.map((data) =>
  //         createParkingData(
  //           data.ParkingId,
  //           data.Total,
  //           data.Reserve,
  //           data.Available
  //         )
  //       );

  //       setRows(updatedRows);
  //     } catch (error) {
  //       console.error("Error fetching parking data from the API", error);
  //     }
  //   };

  //   fetchData();
  // }, []);
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          "http://52.66.50.76:8080/api/CKTCTR/GetParkingData?ParkingId="
        );

        const apiData = response.data;

        const updatedRows = apiData.map((data) =>
          createParkingData(
            data.ParkingId,
            data.Total,
            data.Reserve,
            data.Available
          )
        );

        setRows(updatedRows);
      } catch (error) {
        console.error("Error fetching parking data from the API", error);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const startDate = dateRange[0].startDate.toISOString().split("T")[0];
        const endDate = dateRange[0].endDate.toISOString().split("T")[0];

        const response2 = await axios.get(
          `http://52.66.50.76:8080/api/CKTCTR/GetParkedVehicleqr?ParkingId=&FromDate=${startDate}&ToDate=${endDate}&VehicleNo`
        );

        const apiData2 = response2.data;
        const updatedRows2 = apiData2.map((data) =>
          createVehicleData(
            data.RecId,
            data.ParkingId,
            data.TicketQR,
            data.VehicleNo,
            data.ParkedType,
            data.StartParkedTime,
            data.EndParkedTime,
            data.AccountType,
            data.ParkedAmount
          )
        );

        setOriginalVehicleData(apiData2);
        setFilteredVehicleData(updatedRows2);
        setRows2(updatedRows2);
      } catch (error) {
        console.error("Error fetching vehicle data from the API", error);
      }
    };

    fetchData();
  }, [dateRange]);

  const handleDateChange = (ranges) => {
    setDateRange([ranges.selection]);
  };

  return (
    <TableContainer component={Paper}>
      <DateRangePicker
        ranges={dateRange}
        onChange={handleDateChange}
      />

      <Table aria-label="collapsible table">
        <TableHead>
          <TableRow style={{ border: "1px solid #000000" }}>
            <TableCell
              align="left"
              style={{
                border: "1px solid #000000",
                backgroundColor: "#4f4f4f",
                color: "white",
                fontSize: "15px",
              }}
            >
              View More
            </TableCell>
            <TableCell
              style={{
                border: "1px solid #000000",
                backgroundColor: "#4f4f4f",
                color: "white",
              }}
            >
              Parking ID
            </TableCell>
            <TableCell
              style={{
                border: "1px solid #000000",
                backgroundColor: "#4f4f4f",
                color: "white",
              }}
              align="left"
            >
              Total
            </TableCell>
            <TableCell
              style={{
                border: "1px solid #000000",
                backgroundColor: "#4f4f4f",
                color: "white",
              }}
              align="left"
            >
              Reserve
            </TableCell>
            <TableCell
              style={{
                border: "1px solid #000000",
                backgroundColor: "#4f4f4f",
                color: "white",
              }}
              align="left"
            >
              Available
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row, index) => (
            <Row key={index} row={row} row2={filteredVehicleData} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
